import React, { Component } from 'react';

import { Container } from 'react-bootstrap';

class Footer extends Component {
  render() {
    return (
      <footer className='footer'>
        <Container fluid style={{ marginTop: '2vh' }}>
          <span>
            <p className='copyright pull-right' align='center'>
              &copy; {new Date().getFullYear()} S Steve Alden, all rights
              reserved. Made with {/* ‚ù§‚ù§Ô∏è üíó*/}
              <img
                alt='LOVE'
                src='https://res.cloudinary.com/aldencloud/image/upload/v1585305326/heart.png'
              />
            </p>
          </span>
        </Container>
      </footer>
    );
  }
}

export default Footer;
